import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';

import languages from '../assets/languages.json';

i18n.use(initReactI18next).init({
    resources: {
        en: languages.en,
        es: languages.es,
    },
    lng: 'es',
    fallbackLng: 'en',

    interpolation: {
        escapeValue: false,
    },
});

export default i18n;
